@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Shadows+Into+Light&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#footer{
  margin-top: 15px;
  background-image:   linear-gradient(  rgb(212, 152, 117) 1%, white 99%);
  z-index: 10;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 2.5rem;            /* Footer height */
}

#foot-credit{
  float: right;
  padding-right: 2vw;
}

@media screen and (max-width: 772px) {
  #footer{
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }

  #foot-credit{
    float: left;
    padding-left: 5vw;
  }
}
.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tpage .jumbotron{
    background-image:   linear-gradient(180deg,  rgb(212, 152, 117) ,  white);
}

.tpage .jumbotron .container img{
    width: 150px;
    display: inline;
    margin-right: 25px;
    float: left;
}

.tpage .jumbotron .container h1{
    display: inline;
    font-family: 'Shadows Into Light', cursive;
    font-size: 100px;
    font-weight: bolder;
    background: -webkit-linear-gradient(white, #aaa, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.tpage .container-tpage{
    width: 65%;
    padding: 0;
}

.tpage .tpage-active-sum{
    justify-content: left;
}

.tpage .tpage-btns-row{

    justify-content: space-between;
}

.tpage .tpage-btns-row .btn-secondary{
    margin-top: 5px;
    background-color: rgb(197, 196, 196);
    color:  rgb(155, 107, 46);
    border-color:  rgb(155, 107, 46);
    text-align: center;
    width: 100%;
}

.tpage .tpage-btns-row .btn-outline-secondary{
    margin-top: 5px;
    color:  rgb(155, 107, 46);
    border-color:  rgb(155, 107, 46);
    text-align: center;
    width: 100%;
}

.container-tpage .d-card{
    padding-bottom: 60px;
    overflow: auto;
}

.tpage-info-row{
    margin: 0;
    padding-bottom: 5px;
}

.deleteAll{
    margin-left: auto;
    border-radius: 50px;
    color: rgb(155, 107, 46);
    background-color: rgba(220, 220, 220, 0.363);
    border-color: transparent;
}

.deleteAll:hover{
    border-radius: 50px;
    color: rgb(155, 107, 46);
    background-color: rgba(220, 220, 220, 0.76);
    border-color: transparent;
}

@media screen and (max-width: 772px) {

    .tpage .tpage-active-sum{
        justify-content: center;
    }

    .tpage .tpage-btns-row{

        justify-content: center;
    }

    .tpage .tpage-btns-row > .col-4{
        padding: 0 2px;
    }

    .tpage .tpage-btns-row .btn{
        padding: 0;
    }

    .tpage .jumbotron{
        height: 25vh;
        margin-bottom: 1rem;
    }

    .tpage .jumbotron .container{
        text-align: center;
    }

    .tpage .jumbotron .container img{
        display: inline-block;
        float: none;
        width: 20%;
        margin: auto;
    }

    .tpage .jumbotron .container h1{
        display: inline-block;
        margin: auto;
        font-family: 'Shadows Into Light', cursive;
        font-size: 50px;
        font-weight: bolder;
        background: -webkit-linear-gradient(white, #aaa, #333);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .container-tpage .d-card{
        margin-bottom: 15px;
        overflow: auto;
    }
}
.r-TodoCard{
    width: 100%;
    margin: 0;
    margin-top: 5px;
    padding: 0;
    justify-content: center;
}

.r-TodoCard .card{
    padding-left: 5px;
    padding-right: 5px;
    width: 100%;
    height: 50px;
    align-items: center;
    flex-direction: row;
    text-align: left;

}

.r-TodoCard .form-check-input:checked + .form-check-label{
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
}

.r-TodoCard .card .btn{
    display: none;
    margin-left: auto;
    border-radius: 50%;
    color: red;
    background-color: rgba(220, 220, 220, 0.363);
    border-color: transparent;
}

.r-TodoCard:hover .btn{
    display: inline-block;
}
